import {updateOrder,orderDetail} from '@/utils/api.js'
let jweixin
let agent_cate
let payData
let _thenDo
function initPay () {
	agent_cate = uni.getStorageSync('agent_cate')
	if(agent_cate=='gzh'){
		jweixin = require('jweixin-module');
	}
}
initPay()
function toPay(param,thenDo){
	payData = param
	_thenDo = thenDo
	console.log('----aa-a-'+agent_cate)
	if(agent_cate=='gzh'){
		ghzPay()
	}else if(agent_cate=='micro'){
		microPay()
	}	
}
function payAfter(){
	setTimeout(function(){
		if(_thenDo.cate=='order'){
			updateOrder({cate:1,id:payData.id})
			uni.switchTab({url:'/pages/order/list'})
		}	
		if(_thenDo.cate=='jiazhong'){
			updateOrder({cate:3,id:payData.id})
			uni.redirectTo({url:'/pages/order/detail?id='+payData.id})
		}						
		if(_thenDo.cate=='recharge'){
			updateOrder({cate:2,id:payData.id})
			uni.navigateTo({url:'/pages/balance/record'})
		}
	},1500)		
}
function microPay(){	
	uni.requestPayment({
		provider: 'wxpay',
		timeStamp: payData.timeStamp,
		nonceStr: payData.nonceStr,
		package: payData.package,
		signType: payData.signType,
		paySign: payData.paySign,
		success:(r)=> {			
			uni.showToast({title:''})
			payAfter()
		},
		fail: function (err) {
			console.log('fail:' + JSON.stringify(err));
			uni.showModal({ 
				title:'',
				confirmText:'',
				showCancel:false
			})
		},
		complete:()=> {
			
		}
	})
}
function ghzPay(){
	var self = this;	
	console.log('-----jweixin---')
	console.log(jweixin)
	jweixin.config({
		debug: false, // ,apialertpclogpc
		appId:payData.appId, // 
		timestamp:payData.timeStamp, // 
		nonceStr:payData.nonceStr, // 
		signature:payData.paySign, // 1
		jsApiList: ['chooseWXPay'] // JSJS2
	});
	jweixin.ready(function() {
		jweixin.checkJsApi({
			jsApiList: ['chooseWXPay'], // JSJS2,
			success: function(res) {
				console.log('checkjsapi Success')
				console.log(res);
			},
			fail:function(res) {
				console.log('res')
				console.log(res);
			}
		});
		jweixin.chooseWXPay({
			timestamp: payData.timeStamp, // jssdktimestamptimeStampS
			nonceStr: payData.nonceStr, //  32 
			package: payData.package, // prepay_idprepay_id=***
			signType: payData.signType, // 'SHA1''MD5'
			paySign:payData.paySign, // 
			success:function(res) {
				// 							
				console.log('paysuccess')
				console.log(res)
				payAfter()												
			},
			cancel: function(r) {
			},
			fail:function(res) {						   
				console.log('payfail')
				console.log(res)
				uni.showModal({ 
					title:'',
					showCancel:false,
					success() {
						// uni.switchTab({url:'/pages/order/list'})
					}
				})							
				
			}
		});
	});
	 
	jweixin.error(function(res) {
		console.log('error')
		console.log(res)
		uni.showToast({
			icon: 'none',
			title: '',
			duration: 4000
		});
	});										
}

module.exports = {
	toPay
}